.admin {
    &-header {
        margin-top: 26px;
        margin-bottom: 32px;  
        &-navbar-btn {            
            .material-icons {
                color: $secondary;
            }

        }   
    }
    &-navbar {
        
        &-nav {
        .material-icons:nth-last-child(1){
            margin-left: 26px;
        }
    }        
    }
}


.table tbody tr:hover td, .table tbody tr:hover th {
    background-color:  $primary01;
}

.admin-main-id {   
    .material-icons {
        font-style: 12px;
        color: $secondary;
    }
}
.admin-pagination {
    .page-item.disabled .page-link {
        border-color: $secondary;
    }
    .page-item.active .page-link {
        background-color: $secondary;
    }
    .page-link {
        color: #000;
        border: 1px solid $secondary;
    }
}
.modal {
    &-content {
        padding: 32px 32px 16px 32px;
    }
    
    .form-control, .btn-send {
        height: 51px;
    }
    &-body-view {
        img {
            width: 141px;
            height: 141px;
        }
        // .view-btn:not(:disabled):not(.disabled) {
        //     cursor: pointer;      
        // }
        // a:hover {
        //     color: #000;
        // text-decoration: none;
        //   }
        .btn.focus, .btn:focus  {
            box-shadow: none ;
        }
    }

}
